body {

    &.login {
        background-color: pink;

        .message {
            background-color: #fafafa;
            border-left: 5px solid green;
            font-size: 0.9rem;
            padding: 2em;
            margin-top: 3em;
        }

        #loginform {
            background-color: #fafafa;
        }

        #login {
            width: 95%;
            max-width: 400px;
            margin-left: auto;
            margin-right: auto;
            padding-top: 4%;
        }

        form {
            margin-top: 3em;
            padding: 3em;
        }

        label {
            color: #364250;
            font-weight: 700;
        }

        label input {
            font-weight: normal;
            padding: 0.5em !important;
        }

        input[type=checkbox]:checked {
            position: relative;
        }

        input[type=checkbox]:checked::before {
            content: "\f147";
            position: absolute;
            top: 0;
            left: 0;
            color: green;
        }

        .submit {
            display: block;
        }

        .button {
            background: none;
            background-color: green;
            color: #ffffff;
            display: inline-block;
            font-weight: 700;
            text-transform: uppercase;
            border: none;
            border-bottom: 5px solid #8e0b08;
            border-radius: 5px;
            padding: 1em 2em;
            margin-top: 1em;
            width: 100%;
            height: auto;
            text-shadow: none;
            box-shadow: none;
            float: none;
            transition: all 0.2s ease-in-out;

            &:hover {
                outline: 0;
                background-color: #b41917;
            }
        }

        #nav a,
        #backtoblog a {
            color: #777777;
            transition: all 0.2s ease-in-out;

            &:hover {
                color: green;
            }
        }
    }
}
